.MovieList_element {
  cursor: pointer;
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
}

.MovieList_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  gap: 50px;
}

.MovieList_mov_title {
  font-size: 20px;
  height: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.popup-details {
  width: 90%;
}